import React, { Component } from "react";
import { connect } from "react-redux";
import styled from "styled-components";
import processTweetBodyTemplate from "../../utils/processTweetBodyTemplate";
import bodyTempExampleMd from "./bodyTemp.example.md";
import {
  onChangeTweetBodyTemplate,
  commitTmp
} from "../../redux/settings/action-creaters";

const Layout = styled.div`
  height: 260px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
`;

const LeftSpace = styled.div`
  grid-row: 1;
  grid-column: 1;
  border-right: 1px solid #e6ecf0;
  padding: 8px;
`;

const RightSpace = styled.div`
  grid-row: 1;
  grid-column: 2;
  overflow: scroll;
  padding: 8px;
`;

const Label = styled.label`
  line-height: 24px;
`;

const Description = styled.div`
  padding-top: 4px;
  color: rgb(60, 60, 60);
`;

const DefName = styled.div`
  font-size: 12px;
  font-weight: bold;
  font-style: italic;
`;

const DefDesc = styled.div`
  font-size: 12px;
  margin-top: 2px;
  margin-left: 12px;
  margin-bottom: 4px;
`;

const Textarea = styled.textarea`
  border-radius: 4px;
  margin: 0px;
  height: 40px;
  padding: 4px;
  width: 223px;
  resize: none;
  font-size: 12px;
  font-weight: bold;
  color: rgb(60, 60, 60);

  border: 1px solid #e6ecf0;

  &:active,
  &:focus {
    border: 1px solid #1c94e0;
  }
`;

const PreviewBefore = styled.pre`
  overflow-x: auto;
  white-space: pre-wrap;
  word-wrap: break-word;
  margin: 0;
  background: #f5f8fa;
  padding: 8px;
  border-radius: 4px;
  color: rgb(60, 60, 60);
`;

const PreviewAfter = styled.div`
  background: #f5f8fa;
  padding: 8px;
  border-radius: 4px;
  color: rgb(60, 60, 60);
`;

class TweetBody extends Component {
  componentWillUnmount() {
    this.props.commitTmp();
  }

  render() {
    const { tweetBodyTemplate, onChange } = this.props;
    return (
      <Layout>
        <LeftSpace>
          <Label>Tweet Body Template</Label>
          <Textarea
            placeholder={"{{header}} #TwitterMD"}
            value={tweetBodyTemplate}
            onChange={e => onChange(e.target.value)}
          />
          <Description>
            <DefName>{`{{header}}`}</DefName>
            <DefDesc>Get top header1 from markdown.</DefDesc>
            <DefName>{`{{fm:attr}}`}</DefName>
            <DefDesc>
              Get front matter attribute from markdown. (e.g. {`{{fm:title}}`})
            </DefDesc>
          </Description>
        </LeftSpace>
        <RightSpace>
          <Label>Markdown</Label>
          <PreviewBefore>{bodyTempExampleMd}</PreviewBefore>
          <Label>Tweet Body</Label>
          <PreviewAfter>
            {processTweetBodyTemplate(tweetBodyTemplate, bodyTempExampleMd)}
          </PreviewAfter>
        </RightSpace>
      </Layout>
    );
  }
}

const mapStateToProps = state => {
  return {
    tweetBodyTemplate:
      state.settings.tmp.tweetBodyTemplate || state.settings.tweetBodyTemplate
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onChange: template => {
      dispatch(onChangeTweetBodyTemplate(template));
    },
    commitTmp() {
      dispatch(commitTmp());
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TweetBody);
