import { toast } from "react-toastify";
import saveFile from "file-saver";
import axios from "axios";
import * as ACTIONS from "./actions";
import mdToCanvas from "../../utils/mdToCanvas";
import config from "../../config";
import { ASYNC_ACTION } from "../asyncActionMiddleware";
import processTweetBodyTemplate from "../../utils/processTweetBodyTemplate";

export function togglePreviewMode() {
  return { type: ACTIONS.TOGGLE_PREVIEW_MODE };
}

export function setPreviewUrl(url) {
  return { type: ACTIONS.SET_PREWVIEW_URL, url };
}

export function commitMdTempText() {
  return { type: ACTIONS.COMMIT_MD_TEMP_TEXT };
}

export function setMdTempText(text) {
  return { type: ACTIONS.SET_MD_TEMP_TEXT, text };
}

export function setAccount({ isLogined, profileImage }) {
  return { type: ACTIONS.SET_ACCOUNT, isLogined, profileImage };
}

export function setWaitingTweet(value) {
  return { type: ACTIONS.SET_WAITING_TWEET, value };
}

export function setWaitingLogin(value) {
  return { type: ACTIONS.SET_WAITING_LOGIN, value };
}

export function showPopup(popupComponent) {
  return {
    type: ACTIONS.SHOW_POPUP,
    popupComponent
  };
}

export function closePopup() {
  return { type: ACTIONS.CLOSE_POPUP };
}

export function togglePreview() {
  return {
    type: ASYNC_ACTION,
    async asyncHandler(store, action, next) {
      store.dispatch(commitMdTempText());
      const state = store.getState();
      if (state.app.previewMode) {
        store.dispatch(togglePreviewMode());
        store.dispatch(setPreviewUrl(""));
        return next(action);
      }
      store.dispatch(togglePreviewMode());

      mdToCanvas(state.app.mdText, canvas => {
        canvas.toBlob(blob => {
          const state = store.getState();
          if (state.app.previewMode)
            store.dispatch(setPreviewUrl(URL.createObjectURL(blob)));
        });
      });
      return next(action);
    }
  };
}

export function saveAs() {
  return {
    type: ASYNC_ACTION,
    async asyncHandler(store, action, next) {
      store.dispatch(commitMdTempText());
      const state = store.getState();
      mdToCanvas(state.app.mdText, canvas => {
        canvas.toBlob(blob => {
          saveFile(blob, "download");
        });
      });
      return next(action);
    }
  };
}

export function tweet() {
  return {
    type: ASYNC_ACTION,
    async asyncHandler(store, action, next) {
      store.dispatch(setWaitingTweet(true));
      store.dispatch(commitMdTempText());
      const state = store.getState();
      mdToCanvas(state.app.mdText, canvas => {
        canvas.toBlob(blob => {
          const data = new FormData();
          const title = processTweetBodyTemplate(
            state.settings.tweetBodyTemplate,
            state.app.mdText
          );
          data.append("title", title);
          data.append("image", blob);
          axios
            .post(`${config.apiUrl}/tweet`, data, {
              headers: { "Content-Type": "multipart/form-data" },
              timeout: 10 * 1000
            })
            .then(({ data }) => {
              if (data.ok) {
                toast.info("Tweeted!");
              } else {
                toast.error("Woops! Error!");
              }
              store.dispatch(setWaitingTweet(false));
            })
            .catch(err => {
              toast.error("Woops! Error!");
              store.dispatch(setWaitingTweet(false));
            });
        });
      });
      return next(action);
    }
  };
}

export function login() {
  return {
    type: ASYNC_ACTION,
    async asyncHandler(store, action, next) {
      const loginUrl = `${config.apiUrl}/auth/twitter`;
      chrome.tabs.create({ url: loginUrl });

      return next(action);
    }
  };
}

export function logout() {
  return {
    type: ASYNC_ACTION,
    async asyncHandler(store, action, next) {
      const loginUrl = `${config.apiUrl}/logout`;
      chrome.tabs.create({ url: loginUrl });
      store.dispatch(setAccount({ isLogined: false }));

      return next(action);
    }
  };
}

export function checkLogined() {
  return {
    type: ASYNC_ACTION,
    async asyncHandler(store, action, next) {
      try {
        const { data } = await axios.get(`${config.apiUrl}/check`);
        store.dispatch(
          setAccount({
            isLogined: data.logined,
            profileImage: data.profileImage
          })
        );
      } catch (e) {
        toast.error("Woops! Error!");
      }
      store.dispatch(setWaitingLogin(false));
      return next(action);
    }
  };
}

export function gotoPrivacyPolicy() {
  return {
    type: ASYNC_ACTION,
    async asyncHandler(store, action, next) {
      chrome.tabs.create({ url: config.privacyPolicy });
      return next(action);
    }
  };
}
