import React from "react";
import { connect } from "react-redux";
import { pathResolve } from "./Route";
import { goto } from "./action-creaters";

const Link = props => {
  let { currentPath, to, exact, component, children, goto, onGoto } = props;
  const Component = component;
  to = pathResolve(to);
  let regexText = "^" + to;
  if (exact) regexText += "$";
  const regex = new RegExp(regexText);
  const active = regex.test(currentPath);

  const newProps = Object.assign({}, props);
  delete newProps.onGoto;

  return (
    <Component
      active={active.toString()}
      onClick={() => {
        if (onGoto) onGoto(to);
        goto(to);
      }}
      {...newProps}
    >
      {children}
    </Component>
  );
};

const mapStateToProps = state => {
  return {
    currentPath: state.route.currentPath
  };
};

const mapDispatchToProps = dispatch => {
  return {
    goto(to) {
      dispatch(goto(to));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Link);
