import React from "react";
import { connect } from "react-redux";

const SettingList = ({ customCss, tmp }) => {
  const cssInjection = `
    <style>
      ${tmp.customCss ? tmp.customCss : customCss}
    </style>
  `;
  return <div dangerouslySetInnerHTML={{ __html: cssInjection }} />;
};

const mapStateToProps = state => {
  return {
    customCss: state.settings.customCss,
    tmp: state.settings.tmp
  };
};

const mapDispatchToProps = dispatch => {
  return {};
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SettingList);
