import React from "react";
import styled from "styled-components";
import SettingList from "../components/setting/SettingList";
import CustomCss from "../components/setting/CustomCss";
import TweetBody from "../components/setting/TweetBody";
import Route from "../redux/route/Route";

const SettingLayout = styled.div`
  height: 250px;
  display: grid;
  grid-template-columns: 110px 1fr;
  grid-template-rows: 1fr;
`;

const LeftSpace = styled.div`
  grid-row: 1;
  grid-column: 1;
  border-right: 1px solid #e6ecf0;
`;

const RightSpace = styled.div`
  grid-row: 1;
  grid-column: 2;
`;

const Settings = ({ parentPath }) => {
  return (
    <SettingLayout>
      <LeftSpace>
        <SettingList />
      </LeftSpace>
      <RightSpace>
        <Route exact path={`${parentPath}/`} component={CustomCss} />
        <Route path={`${parentPath}/tweetBody`} component={TweetBody} />
      </RightSpace>
    </SettingLayout>
  );
};

export default Settings;
