import React from "react";
import styled from "styled-components";
import { connect } from "react-redux";
import List from "../atoms/List";
import ListItem from "../atoms/ListItem";
import {
  saveAllSettings,
  commitTmp
} from "../../redux/settings/action-creaters";
import Link from "../../redux/route/Link";

const Layout = styled.div`
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 30px;
`;

const TopSpace = styled.div`
  grid-row: 1;
  grid-column: 1;
`;

const BottomSpace = styled.div`
  grid-row: 2;
  grid-column: 1;
`;

const Button = styled.button`
  width: 100px;
`;

const SettingList = ({ saveAllSettings }) => {
  return (
    <Layout>
      <TopSpace>
        <List>
          <Link exact to="/settings/" component={ListItem}>
            Custom css
          </Link>
          <Link to="/settings/tweetBody" component={ListItem}>
            Tweet body
          </Link>
        </List>
      </TopSpace>
      <BottomSpace>
        <Button className="btn" onClick={saveAllSettings}>
          Save
        </Button>
      </BottomSpace>
    </Layout>
  );
};

const mapStateToProps = state => {
  return {
    state: state.route.currentPath
  };
};

const mapDispatchToProps = dispatch => {
  return {
    saveAllSettings: () => {
      dispatch(commitTmp());
      dispatch(saveAllSettings());
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SettingList);
