export const ACTIONS = {
  GOTO: "ROUTE_GOTO"
};

const routeInitialState = {
  // currentPath: "/settings/tweetBody/"
  currentPath: "/"
};

function route(state = routeInitialState, action) {
  switch (action.type) {
    case ACTIONS.GOTO:
      return {
        ...state,
        currentPath: action.to
      };
    default:
      return {
        ...state
      };
  }
}

export default route;
