import React from "react";
import { connect } from "react-redux";
import { closePopup } from "../redux/app/action-creaters";
import styled from "styled-components";
import ClickOutside from "react-click-outside";

import List from "./atoms/List";
import ListItem from "./atoms/ListItem";
import Divider from "./atoms/Divider";

const Box = styled.div`
  width: 120px;
  height: auto;
  padding-top: 4px;
  padding-bottom: 4px;
  background: white;
  border-radius: 4px;
  border: 1px solid #e6ecf0;
  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.1);
`;

const Icon = styled.i`
  margin-right: 4px;
`;

const Dropdown = props => {
  const items = props.items || [];
  return (
    <ClickOutside onClickOutside={() => props.closePopup()}>
      <Box>
        <List>
          {items.map(item => {
            if (item.divider) {
              return <Divider key={item} />;
            }
            return (
              <ListItem onClick={e => item.onClick(e)} key={item.text}>
                {item.icon ? <Icon className={item.icon} /> : ""}
                {item.text}
              </ListItem>
            );
          })}
        </List>
        {props.children}
      </Box>
    </ClickOutside>
  );
};

const mapStateToProps = state => {
  return {
    popupComponent: state.app.popupComponent,
    isPopupShown: state.app.isPopupShown
  };
};

const mapDispatchToProps = dispatch => {
  return {
    closePopup: () => {
      dispatch(closePopup());
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Dropdown);
