import React from "react";
import { connect } from "react-redux";

export function pathResolve(path) {
  let paths = path.split("/");
  paths = paths.filter(_path => !(_path === "." || _path === ""));
  const suffix = paths.length > 0 ? "/" : "";
  return "/" + paths.join("/") + suffix;
}

const Route = ({ currentPath, path, component, exact = false }) => {
  const Component = component;
  path = pathResolve(path);
  currentPath = pathResolve(currentPath);
  let regexText = "^" + path;
  if (exact) regexText += "$";
  const regex = new RegExp(regexText);
  if (regex.test(currentPath)) {
    return <Component parentPath={path} />;
  } else {
    return "";
  }
};

const mapStateToProps = state => {
  return {
    currentPath: state.route.currentPath
  };
};

const mapDispatchToProps = dispatch => {
  return {};
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Route);
