export const ACTIONS = {
  SET_ALL_SETTINGS: "SET_ALL_SETTINGS",
  SET_TEMP_SETTINGS: "SET_TEMP_SETTINGS"
};

const defaultCSS = `
/* centering image */
.markdown-body img {
  margin-left: auto;
  margin-right: auto;
  display: block;
}
`;

const defaultTweetBodyTemplate = "{{header}} #TwitterMD";

const settingsInitialState = {
  customCss: defaultCSS,
  tweetBodyTemplate: defaultTweetBodyTemplate,
  tmp: {}
};

function settings(state = settingsInitialState, action) {
  switch (action.type) {
    case ACTIONS.SET_ALL_SETTINGS:
      const settings = action.settings;
      const newSettings = {};
      if (settings.customCss !== undefined)
        newSettings.customCss = settings.customCss;
      if (settings.tweetBodyTemplate !== undefined)
        newSettings.tweetBodyTemplate = settings.tweetBodyTemplate;

      return {
        ...state,
        ...newSettings
      };
    case ACTIONS.SET_TEMP_SETTINGS:
      const tmp = action.tmp;
      return {
        ...state,
        tmp: tmp || {}
      };
    default:
      return {
        ...state
      };
  }
}

export default settings;
