import React from "react";
import styled from "styled-components";
import { connect } from "react-redux";
import Dropdown from "./Dropdown";
import { closePopup, gotoPrivacyPolicy } from "../redux/app/action-creaters";
import { goto } from "../redux/route/action-creaters";

const CoverIcon = styled.div`
  position: fixed;
  top: 8px;
  right: 8px;
  width: 30px;
  height: 30px;
  cursor: pointer;
`;

const DropdownContainer = styled.div`
  position: fixed;
  right: 8px;
  top: 50px;
`;

const AppDropdown = props => {
  return (
    <DropdownContainer>
      <Dropdown
        items={[
          {
            text: "Settings",
            onClick() {
              props.gotoSettingPage();
              props.closePopup();
            }
          },
          { divider: true },
          {
            text: "Privacy Policy",
            icon: "fas fa-external-link-alt",
            onClick() {
              props.gotoPrivacyPolicy();
            }
          }
        ]}
      >
        <CoverIcon onClick={props.closePopup} />
      </Dropdown>
    </DropdownContainer>
  );
};

const mapStateToProps = state => {
  return {
    popupComponent: state.app.popupComponent,
    isPopupShown: state.app.isPopupShown
  };
};

const mapDispatchToProps = dispatch => {
  return {
    closePopup: () => {
      dispatch(closePopup());
    },
    gotoPrivacyPolicy: () => {
      dispatch(gotoPrivacyPolicy());
    },
    gotoSettingPage: () => {
      dispatch(goto("/settings"));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AppDropdown);
