import { toast } from "react-toastify";
import { ACTIONS } from "./reducer";
import { ASYNC_ACTION } from "../asyncActionMiddleware";

const SETTING_KEY = "settings";

export function onChangeCustomCss(css) {
  return setTempSettings({
    customCss: css
  });
}

export function onChangeTweetBodyTemplate(template) {
  return setTempSettings({
    tweetBodyTemplate: template
  });
}

export function setAllSettings(settings) {
  return {
    type: ACTIONS.SET_ALL_SETTINGS,
    settings
  };
}

export function setTempSettings(tmp) {
  return {
    type: ACTIONS.SET_TEMP_SETTINGS,
    tmp
  };
}

export function clearTempSettings() {
  return setTempSettings({});
}

export function commitTmp() {
  return {
    type: ASYNC_ACTION,
    async asyncHandler(store, action, next) {
      console.log("commit tmp settings");
      const state = store.getState();
      const tmp = state.settings.tmp;
      // commit tmp
      store.dispatch(setAllSettings(tmp));

      // clear tmp
      store.dispatch(clearTempSettings());

      return next(action);
    }
  };
}

// with chrome storage
export function loadAllSettings() {
  return {
    type: ASYNC_ACTION,
    async asyncHandler(store, action, next) {
      chrome.storage.sync.get(SETTING_KEY, result => {
        console.log("load all settings");
        store.dispatch(setAllSettings(result.settings || {}));
        return next(action);
      });
    }
  };
}

export function saveAllSettings() {
  return {
    type: ASYNC_ACTION,
    async asyncHandler(store, action, next) {
      console.log("save all settings");
      const state = store.getState();
      const settings = Object.assign({}, state.settings);
      settings.tmp = undefined;
      chrome.storage.sync.set({ [SETTING_KEY]: settings }, () => {
        toast.info("Saved!");
        return next(action);
      });
    }
  };
}
