import CodeMirror from "codemirror";
import "codemirror/addon/mode/multiplex";

CodeMirror.defineMode("twitterMDMode", function(config) {
  const texMode = CodeMirror.getMode(config, "stex");
  const mdMode = CodeMirror.getMode(config, "gfm");
  const yamlMode = CodeMirror.getMode(config, "yaml");
  return CodeMirror.multiplexingMode(
    mdMode,
    {
      open: "$$",
      close: "$$",
      mode: texMode
    },
    {
      open: "$",
      close: "$",
      mode: texMode
    },
    {
      open: "---",
      close: "---",
      mode: yamlMode
    }
  );
});
