import html2canvas from "html2canvas";
import frontMatter from "front-matter";
import md from "./md";
import config from "../config";

export default function mdToCanvas(markdown, callback) {
  let content = {};
  try {
    content = frontMatter(markdown);
  } catch (_) {}

  const rendered = md.render(content.body || markdown);
  const parser = new DOMParser();
  const wrapper = parser.parseFromString(rendered, "text/html");
  const nodes = wrapper.body.childNodes;
  const markdownBody = document.createElement("div");
  markdownBody.setAttribute("class", "markdown-body");
  nodes.forEach(node => {
    markdownBody.append(node);
  });
  const invisible = document.createElement("div");
  invisible.setAttribute(
    "style",
    "position: fixed; left: -100%; width: 600px;"
  );
  invisible.appendChild(markdownBody);
  document.body.appendChild(invisible);
  html2canvas(markdownBody, {
    proxy: `${config.apiUrl}/corsProxy`,
    useCORS: true,
    scale: 1.3,
    logging: false
  }).then(canvas => {
    callback(canvas);
    invisible.remove();
  });
}
