import React, { Component } from "react";
import { UnControlled as CodeMirror } from "react-codemirror2";
import { connect } from "react-redux";
import styled from "styled-components";
import md from "../../utils/md";
import exampleMd from "./example.md";
import {
  onChangeCustomCss,
  commitTmp
} from "../../redux/settings/action-creaters";

const Layout = styled.div`
  height: 260px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
`;

const LeftSpace = styled.div`
  grid-row: 1;
  grid-column: 1;
  border-right: 1px solid #e6ecf0;
`;

const RightSpace = styled.div`
  grid-row: 1;
  grid-column: 2;
  overflow: scroll;
`;

class CustomCss extends Component {
  componentWillUnmount() {
    this.props.commitTmp();
  }

  render() {
    const { initialCustomCss, onChange } = this.props;

    const html = md.render(exampleMd);
    return (
      <Layout>
        <LeftSpace>
          <CodeMirror
            className="custom-css-codemirror"
            value={initialCustomCss}
            options={{
              mode: "css",
              theme: "github-light",
              lineNumbers: true,
              lineWrapping: true,
              indentWithTabs: false,
              tabSize: 2
            }}
            editorDidMount={editor => {
              editor.setOption("extraKeys", {
                Tab: cm => {
                  const spaces = Array(cm.getOption("indentUnit") + 1).join(
                    " "
                  );
                  cm.replaceSelection(spaces);
                }
              });
            }}
            onChange={(editor, data, value) => onChange(value)}
          />
        </LeftSpace>
        <RightSpace>
          <div
            className="markdown-body"
            dangerouslySetInnerHTML={{ __html: html }}
          />
        </RightSpace>
      </Layout>
    );
  }
}

const mapStateToProps = state => {
  return {
    initialCustomCss: state.settings.customCss
  };
};

const mapDispatchToProps = dispatch => {
  return {
    onChange: css => {
      dispatch(onChangeCustomCss(css));
    },
    commitTmp() {
      dispatch(commitTmp());
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CustomCss);
