import frontMatter from "front-matter";
const HEADER_REGEX = /^\#\ .{1,50}$/gm;
const LEFT_SHARP = /^\#\ /;

const TEMP_TITLE_REGEX = /\{\{header\}\}/gm;

export function getTitleFromMd(md) {
  const headers = md.match(HEADER_REGEX);
  const topHeader = headers[0] || "";

  return topHeader.replace(LEFT_SHARP, "");
}

export default function processTweetBodyTemplate(template, md) {
  const content = frontMatter(md);
  template = template.replace(TEMP_TITLE_REGEX, getTitleFromMd(content.body));

  for (const prop in content.attributes) {
    const regex = new RegExp(`{{fm:${prop}}}`, "gm");
    template = template.replace(regex, content.attributes[prop]);
  }

  return template.substring(0, 130);
}
