import React, { Component } from "react";
import { connect } from "react-redux";
import { ToastContainer } from "react-toastify";
import { css } from "glamor";

import Route from "./redux/route/Route";
import AppHeader from "./components/AppHeader";
import CssInject from "./components/setting/CssInject";
import WorkspacePage from "./pages/Workspace";
import SettingsPage from "./pages/Settings";

import PopupContainer from "./components/PopupContainer";
import { checkLogined } from "./redux/app/action-creaters";
import { loadAllSettings } from "./redux/settings/action-creaters";

import "./css/App.css";
import "codemirror/lib/codemirror.css";
import "./css/custom-codemirror.css";
import "codemirror/mode/javascript/javascript";
import "codemirror/mode/python/python";
import "codemirror/mode/clike/clike";
import "codemirror/mode/go/go";
import "codemirror/mode/sql/sql";
import "codemirror/mode/dockerfile/dockerfile";
import "codemirror/mode/swift/swift";
import "codemirror/mode/jsx/jsx";
import "codemirror/mode/toml/toml";
import "codemirror/mode/yaml/yaml";
import "codemirror/mode/r/r";
import "codemirror/mode/mathematica/mathematica";
import "codemirror/mode/haskell/haskell";
import "codemirror/mode/rust/rust";
import "codemirror/mode/gfm/gfm";
import "codemirror/mode/css/css";
import "codemirror/mode/stex/stex";
import "codemirror/mode/textile/textile";
import "katex/dist/katex.min.css";
import "./css/github-md.css";
import "./css/github-theme.css";
import "highlightjs/styles/atom-one-light.css";
import "react-toastify/dist/ReactToastify.css";
import "./css/loading.css";
import "@fortawesome/fontawesome-free/css/all.min.css";
import "./utils/twitterMDMode";

class App extends Component {
  componentDidMount() {
    this.props.checkLogined();
    this.props.loadAllSettings();
  }

  render() {
    return (
      <div className="app">
        <AppHeader />
        <ToastContainer
          closeButton={false}
          className={css({
            width: "120px !important"
          })}
          toastClassName={css({
            fontSize: "14px",
            textAlign: "center",
            width: "120px",
            height: "35px",
            minHeight: "35px !important"
          })}
          autoClose={3000}
          hideProgressBar={true}
        />
        <PopupContainer />

        <Route exact path="/" component={WorkspacePage} />
        <Route path="/settings" component={SettingsPage} />

        <CssInject />
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    currentPath: state.route.currentPath
  };
};

const mapDispatchToProps = dispatch => {
  return {
    checkLogined: () => {
      dispatch(checkLogined());
    },
    loadAllSettings: () => {
      dispatch(loadAllSettings());
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);
